import Vue from 'vue';

export default new Vue({
	data() {
		return {
			demoList: [
				{
					type: 1,
					typename: '表单',
					groupVisibity: false,
					comlist: [
						{
							name: 'Numberkeyboard - 数字键盘',
							routename: 'demo-numberkeyboard'
						},
						{
							name: 'Uploader - 图片上传',
							routename: 'demo-uploader'
						}
					]
				},
				{
					typeid: 2,
					typename: '操作反馈',
					groupVisibity: false,
					comlist: [
						{
							name: 'Actionsheet - 行动按钮',
							routename: 'demo-actionsheet'
						},
						{
							name: 'Dialog - 弹出框',
							routename: 'demo-dialog'
						},
						{
							name: 'Picker - 选择器',
							routename: 'demo-picker'
						},
						{
							name: 'PullRefresh - 下拉刷新',
							routename: 'demo-pull-refresh'
						},
						{
							name: 'Toast - 轻提示',
							routename: 'demo-toast'
						}
					]
				},
		        {
		          typeid: 3,
		          typename: '高级组件',
		          groupVisibity: false,
		          comlist: [
		          	{
						name: 'Hotcity - 热门城市',
						routename: 'demo-hotcity'
					},
					{
						name: 'Fixedfilter - 固定筛选',
						routename: 'demo-fixedfilter'
					}
				  ]
		        }
			]
		}
	}
})



